import { RemoveDeviceHandler } from '@gwarm/robot/core';
import { Email, Payload, Result, TaskEnum } from '@gwarm/views/shared';
import { BrowserContext, Page } from 'playwright-core';
import { promisify } from 'util';
const sleep = promisify(setTimeout);
export class GmailRemoveDevicesHandler extends RemoveDeviceHandler {
  name = TaskEnum.REMOVE_DEVICES;
  async handle(
    context: BrowserContext,
    email: Email,
    payload: Payload
  ): Promise<Result> {
    const page = context.pages()[0];
    await page.goto('https://myaccount.google.com/device-activity/?hl=en');
    const locators = await page.locator(
      'div > div:nth-child(1) > div:nth-child(1) > div:nth-child(1) > ul:nth-child(1) > li a'
    );
    const ids = [];
    const count = await locators.count();
    for (let i = 1; i < count; i++) {
      const id = await locators.nth(i).getAttribute('data-device-id');
      ids.push(id);
    }

    for (const id of ids) {
      if (id) await this.removeDevice(page, id);
    }
    return {};
  }

  async removeDevice(page: Page, id: string) {
    await page.goto(
      `https://myaccount.google.com/device-activity/id/${id}?hl=en`
    );
    const locator = page.locator('span:has-text("Sign") >> visible');
    console.log(await locator.count());
    if ((await locator.count()) > 0) {
      await locator.nth(1).click();
      await sleep(1500);
      const confirmlocator = page.locator('button:has-text("Sign") >> visible');
      if ((await confirmlocator.count()) > 0) {
        await confirmlocator.nth(1).click();
      }
    }
    await sleep(1000);
  }
}
