import { Application, Isp } from '@gwarm/robot/core';
import { Module, DynamicModule, Logger, Global } from '@nestjs/common';
const logger = new Logger('ISP');
export const APPLICATION_PROVIDER = 'SCRIPT_APPLICATION';
@Global()
@Module({
  imports: [],
  controllers: [],
  providers: [],
})
export class ScriptModule {
  static async registerAsync(isps: (new () => Isp)[]): Promise<DynamicModule> {
    const application = new Application();
    await application.openBrowser(true);
    for (const isp of isps) {
      const i = new isp();
      application.registerIsp(i);
      logger.log(`${i.name.toUpperCase()} is ready !`);
    }
    return {
      module: ScriptModule,
      providers: [
        {
          provide: APPLICATION_PROVIDER,
          useValue: application,
        },
      ],
      exports: [APPLICATION_PROVIDER],
    };
  }
}
