import { fail, success, Either } from '@gwarm/shared';
import { HandlerEnum } from '../handler';
import { Handler } from '../model';
export class HandlerService {
  private readonly handlers = new Map<HandlerEnum, Handler>();
  signHandler(handler: Handler) {
    this.handlers.set(handler.name, handler);
  }
  getHandler(target: HandlerEnum): Either<Handler, string> {
    const handler = this.handlers.get(target);
    if (handler) return success(handler);
    else return fail('handler not found');
  }
  getHandlers() {
    return this.handlers;
  }
}
