import { ReplyHandler } from '@gwarm/robot/core';
import { ReplyPayload, ReplyResult, Email } from '@gwarm/views/shared';
import { BrowserContext } from 'playwright-core';
import { FilterHandler } from './handlers/filter.handler';
import { MessageService } from './services/message.service';
export class GmailReplyHandler extends ReplyHandler {
  async handle(
    context: BrowserContext,
    email: Email,
    payload: ReplyPayload,
    res: ReplyResult
  ): Promise<ReplyResult> {
    res.count = 0;
    res.limited = false;
    res.blocked = false;
    res.bounce = 0;
    console.log('start reply');
    const page = context.pages()[0];
    const messageService = new MessageService(page);
    const filterHandler = new FilterHandler(page);
    console.log(res.count >= payload.limit);
    while (res.count < payload.limit) {
      await filterHandler.filter(payload.filter);
      await page.waitForNavigation();
      const messages = await messageService.getMessages();
      if (messages.isFail) {
        throw new Error("Couldn't get messages");
      }
      if (messages.value.messages.length === 0) break;
      for (const message of messages.value.messages) {
        if (res.count >= payload.limit) break;
        await messageService.openMessage(message);
        await messageService.openReplyBox();
        if (payload.reset) {
          await messageService.resetReplyBox();
        }
        await messageService.SendMessage(payload.body);
        res.count++;
        console.log('message sent');
      }
    }
    return res;
  }
}
