import {
  Email,
  SetupRulePayload,
  SetupRuleResult,
  TaskEnum,
} from '@gwarm/views/shared';
import { BrowserContext } from 'playwright-core';
import { Handler } from '../core';

export abstract class SetupRuleHandler extends Handler {
  public name = TaskEnum.SETUP_RULE;
  abstract override handle(
    context: BrowserContext,
    email: Email,
    payload: SetupRulePayload,
    result: SetupRuleResult
  ): Promise<SetupRuleResult>;
}
