import { ChangeNameHandler } from '@gwarm/robot/core';
import {
  ChangeNamePayload,
  ChangeNameResult,
  Email,
  TaskEnum,
} from '@gwarm/views/shared';
import { BrowserContext, Page } from 'playwright-core';
export class GmailChangeNameHandler extends ChangeNameHandler {
  public name = TaskEnum.CHANGE_NAME;
  async handle(
    context: BrowserContext,
    email: Email,
    payload: ChangeNamePayload,
    result: ChangeNameResult
  ): Promise<ChangeNameResult> {
    const page = context.pages()[0];
    console.log('[CHANGE_NAME] start');
    await page.goto(
      'https://mail.google.com/mail/u/0/?tab=rm&ogbl#settings/accounts'
    );
    console.log('[CHANGE_NAME] go to accounts');
    await this.waitForReady(page);
    const oldName = await this.getCurrentName(page);
    console.time('[CHANGE_NAME] Delete Old Alias');
    await this.deleteOldAlias(page);
    console.timeEnd('[CHANGE_NAME] Delete Old Alias');
    await page.waitForTimeout(2000);
    console.time('[CHANGE_NAME] Add New Alias');
    await this.addNewAlias(context, email, payload);
    console.timeEnd('[CHANGE_NAME] Add New Alias');
    await page.waitForTimeout(2000);
    console.time('[CHANGE_NAME] Make Last As Default');
    await this.makeLastAsDefault(page);
    console.timeEnd('[CHANGE_NAME] Make Last As Default');
    const newName = await this.getCurrentName(page);
    result.name = newName;
    result.oldName = oldName;
    return result;
  }

  async addNewAlias(
    context: BrowserContext,
    email: Email,
    payload: ChangeNamePayload
  ) {
    const page = context.pages()[0];
    const locators = await page.locator(
      'tr > td > span[role="link"][idlink][tabindex="0"]'
    );
    await locators.nth(0).click();
    console.log("click 'add new alias'");
    await page.waitForTimeout(2000);
    console.log(context.pages().length);
    const newpage = context.pages().pop();
    if (!newpage) throw new Error("can't access add alias page");
    await page.waitForTimeout(2000);
    const inputName = await newpage.locator('#cfn');
    await inputName.nth(0).click({ clickCount: 3 });
    await inputName.nth(0).type(payload.name);
    const inputEmail = await newpage.locator('#focus');
    const alias = this.createNewAliasEmail(email, payload);
    await inputEmail.nth(0).type(alias);
    await newpage.click('input[type="submit"] >> visible=true');
  }

  createNewAliasEmail(
    { email }: Email,
    { withPlus, extension, numberOfPoints }: ChangeNamePayload
  ) {
    let username = email.split('@')[0].split('.').join('');
    const domaine = email.split('@')[1];
    const chunks = [];
    for (let i = 0; i < numberOfPoints; i++) {
      let x = 0;
      let count = 0;
      while (x <= 1 && x < username.length / 1.6 && count < 10) {
        x = Math.floor(Math.random() * username.length);
        count++;
      }
      const parts = username.substring(0, x);
      username = username.substring(x);
      chunks.push(parts);
    }
    chunks.push(username);
    let newusername = chunks.filter((x) => x != '').join('.');
    if (withPlus) {
      newusername += '+' + extension;
    }
    return newusername + '@' + domaine;
  }
  async deleteOldAlias(page: Page) {
    let locators = page.locator(
      'tbody > tr:nth-child(2) > td:nth-child(4) > span'
    );
    console.log('old alias locators :', await locators.count());
    while ((await locators.count()) > 0) {
      await locators.nth(0).click();
      await page.waitForTimeout(2000);
      await page.locator('button[name="ok"]').nth(0).click();
      await page.waitForTimeout(1500);
      locators = page.locator('tbody > tr:nth-child(2) > td:nth-child(4)');
    }
  }
  async makeLastAsDefault(page: Page) {
    const locator = await page.locator(
      'tbody > tr:nth-child(2) > td:nth-child(2) > span[role]'
    );
    if ((await locator.count()) == 0) {
      console.log("can't find last alias");
      return;
    }
    await locator.nth(0).click();
    await page.waitForTimeout(2000);
  }
  async waitForReady(page: Page) {
    await page.waitForSelector('table > tbody input');
    console.log('browser is ready');
  }

  async getCurrentName(page: Page): Promise<string> {
    const locator = page.locator(
      'tbody>tr:nth-child(4)>td:nth-child(2)>table:nth-child(1)>tbody>tr',
      {
        has: page.locator('td:nth-child(4)'),
      }
    );
    const count = await locator.count();
    switch (count) {
      case 0:
        throw new Error("can't find current name");
      case 1:
        return await locator.locator('td:first-child').nth(0).innerText();
      default:
        return await page
          .locator(
            'tbody>tr:nth-child(4)>td:nth-child(2)>table:nth-child(1)>tbody>tr',
            {
              has: page.locator('td:nth-child(2)>span:not([role])'),
            }
          )
          .locator('td:first-child')
          .nth(0)
          .innerText();
    }
  }
}
