import { BrowserContext, Page } from 'playwright-core';
import { HandlerService } from './handler.service';
import { RoutingService } from './routing.service';
import { AuthResult, Email } from '@gwarm/views/shared';
import { HandlerEnum } from '../handler';
import { Either, fail, success } from '@gwarm/shared';
import { deflateSync } from 'zlib';
import { promisify } from 'util';
const sleep = promisify(setTimeout);

export class AuthService {
  constructor(
    private readonly page: Page,
    private readonly RoutingService: RoutingService,
    private readonly handlerService: HandlerService
  ) {}

  async Authenticate(email: Email): Promise<Either<string, string>> {
    await this.page.goto(
      'https://accounts.google.com/AccountChooser/signinchooser?service=mail&continue=https%3A%2F%2Fmail.google.com%2Fmail%2F&flowName=GlifWebSignIn&flowEntry=AccountChooser&hl=en',
      {
        waitUntil: 'commit',
      }
    );
    const loop = true;
    while (loop) {
      console.log('Next Loop!');
      const HANDLER_NAME_RESULT = await this.RoutingService.waitForNextHandler(
        this.handlerService.getHandlers()
      );
      if (HANDLER_NAME_RESULT.isFail) {
        return fail('Error : No Handler is created Yet');
      }

      const HANDLER_NAME = HANDLER_NAME_RESULT.value;
      console.log('Handler :', HANDLER_NAME);
      if (HANDLER_NAME === HandlerEnum.END) {
        return success('Authenticated successfully');
      } else if (HANDLER_NAME === HandlerEnum.TIMEOUT) {
        return fail('Timeout');
      } else {
        const handler = this.handlerService.getHandler(HANDLER_NAME);
        if (handler.isFail) return fail(handler.error);
        else {
          const validationResult = await handler.value.validate();
          if (validationResult.isFail)
            return fail(`Handler ${HANDLER_NAME} is Wrong`);
          const result = await handler.value.handle();
          if (result.isFail) return fail(result.error);
          if (result.value) {
            const resultNext = await this.clickNext();
            if (resultNext.isFail) return success('Authenticated successfully');
          }
        }
      }
    }
    return success('Authenticated successfully');
  }

  async clickNext(): Promise<Either<string, string>> {
    const locator = await this.page.locator('text=Next');
    if ((await locator.count()) == 0) return fail('Next button not found');
    locator.nth(0).click();
    return success('Clicked next');
  }
  async getResult(context: BrowserContext): Promise<AuthResult> {
    const result = new AuthResult();
    const deflated = deflateSync(
      JSON.stringify(JSON.stringify(await context.storageState()))
    ).toString('base64');
    result.token = deflated;
    return result;
  }
}
