import { Handler } from '../core/handler';
import { BrowserContext } from 'playwright-core';
import {
  Email,
  ReplyResult,
  ReplyPayload,
  TaskEnum,
} from '@gwarm/views/shared';
export abstract class ReplyHandler extends Handler {
  public name = TaskEnum.REPLY;
  abstract override handle(
    context: BrowserContext,
    email: Email,
    payload: ReplyPayload,
    res: ReplyResult
  ): Promise<ReplyResult>;
}
