import { AuthHandler } from '@gwarm/robot/core';
import { AuthPayload, AuthResult, Email, TaskEnum } from '@gwarm/views/shared';
import { BrowserContext, Page } from 'playwright-core';

export class HotmailAuthHandler extends AuthHandler {
  public override name = TaskEnum.AUTHENTICATION;
  async handle(
    context: BrowserContext,
    email: Email,
    payload: AuthPayload
  ): Promise<AuthResult> {
    const page = context.pages()[0];
    await this.goToLoginPage(page);
    await this.fillEmail(page, email.email);
    await this.fillPassword(page, email.password);

    await page.waitForTimeout(7978988);
    return new AuthResult();
  }
  async goToLoginPage(page: Page) {
    await page.goto('https://login.live.com/login.srf');
  }
  async fillEmail(page: Page, email: string) {
    await page.click('input[type="email"]');
    await page.fill('input[type="email"]', email);
    await page.click('text=Next');
    await page.waitForEvent('framenavigated', () => {
      return true;
    });
    console.log('frame navigated');
  }

  async fillPassword(page: Page, password: string) {
    await page.click('input[type="password"]');
    await page.fill('input[type="password"]', password);
    await page.click('text=Sign in');
    await page.waitForResponse((res) => {
      console.log(res.status());
      return false;
    });
  }
}
