import { Either, fail, success } from '@gwarm/shared';
import { Response } from 'playwright-core';
import { promisify } from 'util';
import { Handler } from '../model';
import { HandlerEnum } from './handler.enum';
const sleep = promisify(setTimeout);
export class EmailHandler extends Handler {
  name = HandlerEnum.EMAIL_HANDLER;
  async handle(): Promise<Either<boolean, string>> {
    const locator = await this.page.locator('input[type="email"]');
    if ((await locator.count()) == 0) return fail('Email input not found');
    await locator.nth(0).type(this.email.email);
    return success(true);
  }
  async waitFor(res: Response): Promise<boolean> {
    const payload = res.request().postData();
    if (payload) {
      return payload.includes('accounts.google.com/v3/signin/identifier');
    }
    return false;
  }

  async validate(): Promise<Either<boolean, string>> {
    try {
      const input = await this.page.waitForSelector('input[type="email"]', {
        timeout: 5000,
      });
      if (input) return success(true);
      else return fail('Email input not found');
    } catch (e: any) {
      return fail(e.message);
    }
  }
}
