import {
  DynamicModule,
  Global,
  Logger,
  Module,
  OnApplicationShutdown,
  OnModuleDestroy,
  BeforeApplicationShutdown,
} from '@nestjs/common';
import { Connection, connect, Channel, Replies } from 'amqplib';
import { RabbitmqModuleOption } from './interfaces/rabbitmq-module.option';
@Global()
@Module({
  imports: [],
  providers: [],
  exports: [],
})
export class RabbitMQModule
  implements OnApplicationShutdown, OnModuleDestroy, BeforeApplicationShutdown
{
  public static async forRootAsync(
    options: RabbitmqModuleOption
  ): Promise<DynamicModule> {
    let connection: Connection;
    try {
      connection = await connect(options.uri);
    } catch (e) {
      Logger.log('RabbitMQ connection closed :', e);
      process.exit(1);
    }
    const channel: Channel = await connection.createChannel();
    Logger.log(`Connected to RabbitMQ server`, 'RabbitMQModule');
    const p: Promise<Replies.AssertQueue>[] = [];
    for (const task of options.tasks) {
      p.push(
        channel.assertQueue(task, {
          durable: true,
        })
      );
    }
    p.push(
      channel.assertQueue('RESPONSE', {
        durable: true,
      })
    );
    p.push(
      channel.assertQueue('UNDO', {
        durable: true,
      })
    );
    await Promise.all(p);
    Logger.log(`Asserted queues`, 'RabbitMQModule');

    return {
      module: RabbitMQModule,
      imports: [],
      providers: [
        {
          provide: 'RABBITMQ_CHANNEL',
          useValue: channel,
        },
      ],
      exports: ['RABBITMQ_CHANNEL'],
    };
  }

  public onModuleDestroy() {
    Logger.log('RabbitMQModule destroyed');
  }

  public onApplicationShutdown() {
    Logger.log('RabbitMQModule shutdown');
  }
  public beforeApplicationShutdown() {
    Logger.log('RabbitMQModule before shutdown');
  }
}
