import { ChangeNameHandler } from '@gwarm/robot/core';
import {
  ChangeNamePayload,
  ChangeNameResult,
  Email,
  TaskEnum,
} from '@gwarm/views/shared';
import { BrowserContext } from 'playwright-core';
export class ProtonChangeNameHandler extends ChangeNameHandler {
  public name = TaskEnum.CHANGE_NAME;
  async handle(
    context: BrowserContext,
    email: Email,
    payload: ChangeNamePayload,
    result: ChangeNameResult
  ): Promise<ChangeNameResult> {
    const page = context.pages()[0];
    console.log('[CHANGE_NAME] start');
    await page.goto('https://account.proton.me/u/0/mail/identity-addresses');
    await page.waitForSelector('#displayName');
    await page.click('#displayName', {
      clickCount: 3,
    });
    await page.type('#displayName', payload.name);
    await page.click(
      '#name-signature > div > form > div:nth-child(2) > div.settings-layout-right > button'
    );
    await page.waitForTimeout(3000);
    result.name;
    return result;
  }
}
