import { ApiProperty } from '@nestjs/swagger';
import { Response } from '../core';
import { TaskEnum } from '../enums';
import { ReplyResult } from '../results';

export class ReplyResponse extends Response {
  @ApiProperty({
    type: TaskEnum.REPLY,
    default: TaskEnum.REPLY,
  })
  override action: TaskEnum.REPLY;
  @ApiProperty()
  override result: ReplyResult;
}
