import { Either, fail, success } from '@gwarm/shared';
import { Response } from 'playwright-core';
import { Handler } from '../model';
import { HandlerEnum } from './handler.enum';

export class PasswordWrongHandler extends Handler {
  name = HandlerEnum.PASSWORD_WRONG_HANDLER;

  async handle(): Promise<Either<boolean, string>> {
    return fail('Password is wrong');
  }
  async waitFor(res: Response): Promise<boolean> {
    if (res.url().includes('pwd')) {
      const body = await res.text();
      if (
        body.toLowerCase().includes('INCORRECT_ANSWER_ENTERED'.toLowerCase()) &&
        body.toLowerCase().includes('FIRST_AUTH_FACTOR'.toLowerCase())
      ) {
        return true;
      }
    }
    return false;
  }
  async validate(): Promise<Either<boolean, string>> {
    try {
      const input = await this.page.waitForSelector(
        'input[type="password"] >> visible=true',
        {
          timeout: 5000,
        }
      );
      if (input) return success(true);
      else return fail('password input not found');
    } catch (e: any) {
      return fail(e.message);
    }
  }
}
