import { ApiPropertyOptional } from '@nestjs/swagger';

export class ReportingInboxPyload {
  @ApiPropertyOptional({
    default: 0,
    minimum: 0,
    maximum: 100,
  })
  archive?: number;
  @ApiPropertyOptional({
    default: 0,
    minimum: 0,
    maximum: 100,
  })
  delete?: number;
  @ApiPropertyOptional({
    default: 0,
    minimum: 0,
    maximum: 100,
  })
  star?: number;
  @ApiPropertyOptional({
    default: 0,
    minimum: 0,
    maximum: 100,
  })
  important?: number;
}
