import {
  ApiOperation,
  ApiResponse,
  ApiResponseMetadata,
  ApiOperationOptions,
} from '@nestjs/swagger';

import { applyDecorators } from '@nestjs/common';

export type ApiOperationAndResponse = {
  responseMetadata: ApiResponseMetadata;
  operationOptions: ApiOperationOptions;
};

export const ApiWithTokenResponse = (
  options: ApiOperationAndResponse
): MethodDecorator => {
  return applyDecorators(
    ApiOperation(options.operationOptions),
    ApiResponse({ status: 200, ...options.responseMetadata })
  );
};
