import { Either, fail, success } from '@gwarm/shared';
import { Response } from 'playwright-core';
import { Handler } from '../model';
import { HandlerEnum } from './handler.enum';

export class ConfirmationEmailHandler extends Handler {
  name = HandlerEnum.CONFIRMATION_EMAIL_HANDLER;
  async handle(): Promise<Either<boolean, string>> {
    const locator = this.page.locator('input[type="email"] >> visible=true');
    if ((await locator.count()) == 0)
      return fail('input Email challenge not found');
    await locator.nth(0).type(this.email.confirmationEmail);
    return success(true);
  }
  async waitFor(res: Response): Promise<boolean> {
    const payload = res.request().postData();
    if (payload) {
      return payload.includes('signin/v2/challenge/kpe');
    }
    return false;
  }
  async validate(): Promise<Either<boolean, string>> {
    try {
      const input = await this.page.waitForSelector(
        'input[type="email"] >> visible=true',
        {
          timeout: 5000,
        }
      );
      if (input) return success(true);
      else return fail('Input not Found!');
    } catch (e: any) {
      console.log(e);
      return fail(e.message);
    }
  }
}
