import { ApiProperty } from '@nestjs/swagger';
import { IsBoolean, IsNumber } from 'class-validator';
export class PaginationResult<T> {
  @ApiProperty()
  @IsNumber()
  size: number;
  @ApiProperty()
  @IsNumber()
  page: number;
  @ApiProperty()
  @IsNumber()
  totalItems: number;
  @ApiProperty()
  @IsNumber()
  totalPages: number;
  @ApiProperty()
  @IsBoolean()
  hasPrev: boolean;
  @ApiProperty()
  @IsBoolean()
  hasNext: boolean;
  @ApiProperty()
  content: T[];
}
