import { AppModule } from './app/app.module';
import { NestFactory } from '@nestjs/core';
import { config } from 'dotenv';
import { TaskService } from './app/task.service';
import { readFileSync } from 'fs';
import { join } from 'path';
async function run() {
  try {
    const task = JSON.parse(
      readFileSync(join(__dirname, 'assets', 'task.json'), 'utf8')
    );
    console.log(task);
    config();
    const module = await NestFactory.create(AppModule);

    module.enableShutdownHooks();
    const taskService = await module.get<TaskService>(TaskService);
    const res = await taskService.executeTask(task);

    await module.close();
    console.log(res);
  } catch (e) {
    console.error('Can Not Parse Task', e);
  }
  process.exit(0);
}
run();
