import { ApiProperty, ApiPropertyOptional } from '@nestjs/swagger';
import { IsEmail, IsNotEmpty, IsString } from 'class-validator';

export class Email {
  @ApiProperty()
  @IsEmail()
  @IsNotEmpty()
  email!: string;
  @ApiProperty()
  @IsString()
  @IsNotEmpty()
  password!: string;
  @ApiProperty()
  @IsEmail()
  @IsNotEmpty()
  confirmationEmail!: string;
  @ApiPropertyOptional()
  @IsEmail()
  @IsNotEmpty()
  session?: string;
}
