import { Task, IspEnum, Response, Result, TaskEnum } from '@gwarm/views/shared';
import { BrowserManager } from '../managers/browser.manager';
import { Isp } from './isp';
export class Application {
  private applicationId: number;
  private isps = new Map<IspEnum, Isp>();
  private browserManager = new BrowserManager();
  private results = new Map<number, Result>();
  private tasks = new Map<number, Task>();
  private responses = new Map<number, Response>();
  private runningTask: Task | undefined;
  openBrowser(headless = true) {
    return this.browserManager.openBrowser(headless);
  }
  public registerIsp(isp: Isp) {
    this.isps.set(isp.name, isp);
  }

  public async executeTask(task: Task): Promise<Response> {
    if (this.runningTask)
      throw new Error(`Task ${this.runningTask.id} is running`);
    this.runningTask = task;
    this.tasks.set(task.id, task);
    const result = new Result();
    const response = new Response();
    this.responses.set(task.id, response);
    this.results.set(task.id, result);
    const isp = this.isps.get(task.isp);
    if (!isp) throw new Error(`ISP ${task.isp} not found`);
    const controller = isp.getController();
    const handler = controller.getHandler(task.action);
    if (!handler)
      throw new Error(`Handler ${task.action} for ${task.isp} is not found`);
    const { context, stoper } = await this.browserManager.createNewContext(
      task.id,
      task.email.session
    );
    await this.browserManager.createNewPage(context);
    const tmp = await controller.execute(
      context,
      task,
      stoper,
      result,
      response
    );
    console.log(tmp);
    await this.browserManager.closeContext(task.id);
    this.runningTask = undefined;
    return response;
  }
  setApplicationId(id: number) {
    this.applicationId = id;
  }
  getApplicationId() {
    return this.applicationId;
  }
  async stopTaskById(id: number) {
    const resolver = this.browserManager.getResolver(id);
    console.log(`resolver`, resolver, id);
    if (!resolver) throw new Error('Resolver not found');
    await resolver(this.results.get(id) ?? new Result());
    await this.browserManager.closeContext(id);
  }

  getTasks() {
    return [...this.tasks.values()];
  }
  getTask(id: number) {
    return this.tasks.get(id);
  }

  getTasksByTaskName(action: TaskEnum) {
    return [...this.tasks.values()].filter((task) => task.action === action);
  }

  getResultByTaskId(id: number) {
    console.log(this.results);
    return this.results.get(id);
  }
  getResultByJobId(id: number) {
    const task = [...this.tasks.values()].find((task) => task.jobId === id);
    if (!task) return undefined;
    return this.results.get(task.id);
  }

  getResponseByTaskId(id: number) {
    return this.responses.get(id);
  }
  getResponseByJobId(id: number) {
    const task = [...this.tasks.values()].find((task) => task.jobId === id);
    if (!task) return undefined;
    return this.responses.get(task.id);
  }
  getTaskByJobId(id: number) {
    return [...this.tasks.values()].find((task) => task.jobId === id);
  }
  stopTaskByJobId(id: number) {
    const task = this.getTaskByJobId(id);
    if (!task) return;
    return this.stopTaskById(task.id);
  }
}
