import { AuthHandler } from '@gwarm/robot/core';
import { AuthPayload, AuthResult, Email } from '@gwarm/views/shared';
import { BrowserContext, Page } from 'playwright-core';
import {
  ChallengeHandler,
  ConfirmationEmailHandler,
  ConfirmationEmailWrongHandler,
  DisabledAccountHandler,
  EmailHandler,
  EmailWrongHandler,
  EndHandler,
  PasswordHandler,
  PasswordWrongHandler,
  PhoneHandler,
} from './handler';
import { AuthService, HandlerService, RoutingService } from './service';
export class GmailAuthHandler extends AuthHandler {
  async handle(
    context: BrowserContext,
    email: Email,
    payload: AuthPayload,
    result: AuthResult
  ): Promise<AuthResult> {
    const page = context.pages()[0];
    const routingService = new RoutingService(page);
    const handlerService = new HandlerService();
    handlerService.signHandler(new EmailHandler(page, email));
    handlerService.signHandler(new EmailWrongHandler(page, email));
    handlerService.signHandler(new PasswordHandler(page, email));
    handlerService.signHandler(new PasswordWrongHandler(page, email));
    handlerService.signHandler(new ChallengeHandler(page, email));
    handlerService.signHandler(new ConfirmationEmailHandler(page, email));
    handlerService.signHandler(new ConfirmationEmailWrongHandler(page, email));
    handlerService.signHandler(new PhoneHandler(page, email));
    handlerService.signHandler(new DisabledAccountHandler(page, email));
    handlerService.signHandler(new EndHandler(page, email));

    const authService = new AuthService(page, routingService, handlerService);
    const content = await authService.Authenticate(email);
    if (content.isFail) {
      result.error = content.error;
      throw new Error(content.error);
    }
    const token = await authService.getResult(context);
    result.token = token.token;
    result.error = token.error;
    return result;
  }
}
