import { ApiPropertyOptional } from '@nestjs/swagger';
import { Payload } from '../core';
import { ReportingInboxPyload } from './reporting/inbox.payloads';
import { ReportingPromotionsPayload } from './reporting/promotion.payloads';
import { ReportingSpamPayload } from './reporting/spam.payloads';

export class ReportingPayload extends Payload {
  inbox: ReportingInboxPyload;
  filter: ReportingFilterPayload;
  spam: ReportingSpamPayload;
  promotion: ReportingPromotionsPayload;
}

export class ReportingFilterPayload {
  @ApiPropertyOptional()
  subject?: string;
  @ApiPropertyOptional()
  from?: string;
  @ApiPropertyOptional()
  email?: string;
  @ApiPropertyOptional({
    default: new Date().setDate(new Date().getDate() - 1),
  })
  date: number;
}
