import { readFileSync } from 'fs';
import { createHash } from 'crypto';
import {
  Browser,
  BrowserContext,
  BrowserContextOptions,
  firefox,
} from 'playwright-core';
import { join } from 'path';
import { inflateSync } from 'zlib';
import { Result } from '@gwarm/views/shared';
class Context {
  context: BrowserContext;
  stoper: Promise<Result>;
}
export class BrowserManager {
  private browser: Browser;
  private contexts = new Map<number, BrowserContext>();
  private stopers = new Map<number, Promise<Result>>();
  private resolvers = new Map<number, (result: Result) => void>();
  async openBrowser(headless = true) {
    this.browser = await firefox.launch({
      headless: headless,
      args: ['--lang="it-IT"'],
    });
    return this.browser;
  }
  async createNewContext(taskId: number, session?: string): Promise<Context> {
    const options: BrowserContextOptions = {
      locale: 'en-US',
      bypassCSP: true,
      colorScheme: 'dark',
      acceptDownloads: true,
      permissions: ['geolocation'],
      reducedMotion: 'reduce',
      extraHTTPHeaders: {
        'accept-language': 'en-US,en;q=0.9',
      },
    };
    if (session) {
      const s = JSON.parse(
        inflateSync(Buffer.from(session, 'base64')).toString()
      );
      const state = JSON.parse(s);
      options.storageState = state;
    }

    const context = await this.browser.newContext(options);
    const contextId = taskId;
    this.contexts.set(contextId, context);
    const stoper = new Promise<Result>((resolve) => {
      this.resolvers.set(contextId, resolve);
    });
    this.stopers.set(contextId, stoper);
    return { context, stoper };
  }

  getContextId(context: BrowserContext) {
    return createHash('md5').update(JSON.stringify(context)).digest('hex');
  }

  async createNewPage(context: BrowserContext) {
    const page = await context.newPage();
    const intialScript = readFileSync(
      join(__dirname, 'utils/initial-context.js')
    ).toString();
    await page.addInitScript(intialScript);
  }
  async closeContext(id: number) {
    const context = this.contexts.get(id);
    this.contexts.delete(id);
    context?.close();
  }

  async closeBrowser() {
    await this.browser.close();
  }
  getResolver(id: number) {
    console.log(`getResolver`, this.resolvers);
    return this.resolvers.get(id);
  }
}
