import { Controller, Handler } from '../core';
import { IspEnum, TaskEnum } from '@gwarm/views/shared';

export class Isp {
  protected controller: Controller<Handler>;
  public name: IspEnum;
  protected readonly handlers: (new () => Handler)[];

  constructor() {
    const HANDLERS = new Map<TaskEnum, Handler>();
    console.log(this.handlers);
    for (const handler of this.handlers) {
      const h = new handler();
      HANDLERS.set(h.name, h);
    }
    this.controller = new Controller(HANDLERS);
  }

  getController() {
    return this.controller;
  }
}
