import { Handler } from '../core/handler';
import { BrowserContext } from 'playwright-core';
import { Email, Payload, Result, TaskEnum } from '@gwarm/views/shared';
export abstract class RemoveDeviceHandler extends Handler {
  public name = TaskEnum.REMOVE_DEVICES;
  abstract override handle(
    context: BrowserContext,
    email: Email,
    payload: Payload
  ): Promise<Result>;
}
