import { Handler } from '../core/handler';
import { AuthPayload, AuthResult, Email, TaskEnum } from '@gwarm/views/shared';
import { BrowserContext } from 'playwright-core';

export abstract class AuthHandler extends Handler {
  public name = TaskEnum.AUTHENTICATION;
  abstract override handle(
    context: BrowserContext,
    email: Email,
    payload?: AuthPayload,
    result?: AuthResult
  ): Promise<AuthResult>;
}
