import { ApiProperty } from '@nestjs/swagger';
import {
  IsBoolean,
  IsNotEmpty,
  IsNumber,
  IsPositive,
  IsString,
} from 'class-validator';
import { Payload } from '../core/payload';

class Placeholder {
  @ApiProperty({ type: String })
  key: string;
  @ApiProperty({ type: String })
  value: string[];
}

export class ReplyPayload extends Payload {
  @ApiProperty()
  @IsString()
  @IsNotEmpty()
  filter!: string;
  @ApiProperty({
    isArray: true,
    type: Placeholder,
  })
  placeholders: Placeholder[];
  @ApiProperty()
  @IsString()
  @IsNotEmpty()
  body!: string;
  @ApiProperty({ default: 500 })
  @IsNumber()
  @IsPositive()
  limit!: number;
  @ApiProperty({ default: false })
  @IsBoolean()
  reset!: boolean;
}
