import { RabbitMQModule } from '@gwarm/packages/rabbitmq';
import { Gmail } from '@gwarm/robot/gmail';
import { Hotmail } from '@gwarm/robot/hotmail';
import { Module } from '@nestjs/common';
import { ScriptModule } from './script.module';
import { Proton } from '@gwarm/robot/proton';
import { TaskService } from './task.service';
@Module({
  imports: [
    ScriptModule.registerAsync([Gmail, Hotmail, Proton]),
    RabbitMQModule.forRootAsync({
      uri: process.env.RABBITMQ_URI as string,
      tasks: [],
    }),
  ],
  controllers: [],
  providers: [TaskService],
})
export class AppModule {}
