import { Either, fail, success } from '@gwarm/shared';
import { Response } from 'playwright-core';
import { Handler } from '../model';
import { HandlerEnum } from './handler.enum';

export class PhoneHandler extends Handler {
  name = HandlerEnum.PHONE_HANDLER;

  async handle(): Promise<Either<boolean, string>> {
    return fail('Phone number is required');
  }
  async waitFor(res: Response): Promise<boolean> {
    if (res.url().includes('challenge') && !res.url().includes('password')) {
      const value = await res.headerValue('google-accounts-signin');
      const body = await res.text();
      if (
        !value &&
        body.includes('speedbump/idvreenable') &&
        !body
          .toLocaleLowerCase()
          .includes(this.email.email.toLocaleLowerCase()) &&
        !body
          .toLocaleLowerCase()
          .includes('LOGIN_CHALLENGE'.toLocaleLowerCase()) &&
        !body
          .toLocaleLowerCase()
          .includes('INCORRECT_ANSWER_ENTERED'.toLocaleLowerCase())
      )
        return true;
    }
    return false;
  }
  async validate(): Promise<Either<boolean, string>> {
    try {
      const input = await this.page.waitForSelector(
        'input[name="deviceAddress"] >> visible=true',
        {
          timeout: 5000,
        }
      );
      if (input) return success(true);
      else return fail('input not found');
    } catch (e: any) {
      return fail(e.message);
    }
  }
}
