import { AuthHandler } from '@gwarm/robot/core';
import { AuthPayload, AuthResult, Email, TaskEnum } from '@gwarm/views/shared';
import { BrowserContext } from 'playwright-core';
import { deflateSync } from 'zlib';

export class ProtonAuthHandler extends AuthHandler {
  public override name = TaskEnum.AUTHENTICATION;
  async handle(
    context: BrowserContext,
    email: Email,
    payload: AuthPayload,
    result: AuthResult
  ): Promise<AuthResult> {
    console.log('START LOGIN');
    const page = context.pages()[0];
    await page.goto('https://account.proton.me/login?language=en&product=mail');
    await page.waitForSelector('input[id="username"]');
    await page.type('input[id="username"]', email.email);
    await page.type('input[type="password"]', email.password);
    await page.click('button[type="submit"]');
    await page.waitForSelector(
      'body > div.app-root > div.flex.flex-row.flex-nowrap.h100 > div > div > div > div.sidebar.flex.flex-nowrap.flex-column.no-print.outline-none > div.flex-item-fluid.flex-nowrap.flex.flex-column.scroll-if-needed.pb1 > nav > div > ul > li:nth-child(1)'
    );
    console.log('Logged in');
    const deflated = deflateSync(
      JSON.stringify(JSON.stringify(await context.storageState()))
    ).toString('base64');
    result.token = deflated;
    return result;
  }
}
