import { Either } from '@gwarm/shared';
import { Email } from '@gwarm/views/shared';
import { Page, Response } from 'playwright-core';
import { HandlerEnum } from '../handler';
export abstract class Handler {
  abstract name: HandlerEnum;
  constructor(protected readonly page: Page, protected readonly email: Email) {}
  abstract handle(): Promise<Either<boolean, string>>;
  abstract waitFor(res: Response): Promise<boolean>;
  abstract validate(): Promise<Either<boolean, string>>;
}
