import { Task } from '../core';
import { ApiProperty } from '@nestjs/swagger';
import { AuthPayload } from '../payloads';
import { TaskEnum } from '../enums';
export class AuthTask extends Task {
  @ApiProperty({
    type: TaskEnum.AUTHENTICATION,
    default: TaskEnum.AUTHENTICATION,
  })
  override action: TaskEnum.AUTHENTICATION = TaskEnum.AUTHENTICATION;
  @ApiProperty()
  override payload: AuthPayload;
}
