import { Page, Response } from 'playwright-core';
import { Handler } from '../model';
import { HandlerEnum } from '../handler';
import { Either, success, fail } from '@gwarm/shared';
export class RoutingService {
  constructor(private readonly page: Page) {}
  private passedHandlers: Handler[] = [];

  async waitForNextHandler(
    handlers: Map<HandlerEnum, Handler>
  ): Promise<Either<HandlerEnum, string>> {
    const promotedHandlers: Handler[] = [];
    let response: Response;
    try {
      response = await this.page.waitForResponse(
        async (res) => {
          for (const handler of handlers) {
            if (
              (await handler[1].waitFor(res)) &&
              !this.passedHandlers.includes(handler[1])
            ) {
              this.passedHandlers.push(handler[1]);
              return true;
            }
          }
          return false;
        },
        {
          timeout: 15000,
        }
      );
    } catch (e) {
      return fail('Timeout waiting for next handler');
    }
    for (const handler of handlers) {
      if (await handler[1].waitFor(response)) {
        return success(handler[0]);
      }
    }
    return success(HandlerEnum.TIMEOUT);
  }
}
