import { Either, fail, success } from '@gwarm/shared';
import { Response } from 'playwright-core';
import { Handler } from '../model';
import { HandlerEnum } from './handler.enum';

export class ConfirmationEmailWrongHandler extends Handler {
  name = HandlerEnum.CONFIRMATION_EMAIL_WRONG_HANDLER;
  async handle(): Promise<Either<boolean, string>> {
    console.log('Confirmation Email is wrong');
    return fail('Confirmation Email is wrong');
  }
  async waitFor(res: Response): Promise<boolean> {
    if (res.url().includes('signin/challenge')) {
      const body = (await res.text()).toLowerCase();
      if (
        body.includes(this.email.email.toLowerCase()) &&
        body.includes('INCORRECT_ANSWER_ENTERED'.toLocaleLowerCase()) &&
        body.includes('LOGIN_CHALLENGE'.toLocaleLowerCase())
      ) {
        return true;
      }
    }
    return false;
  }
  async validate(): Promise<Either<boolean, string>> {
    try {
      const input = await this.page.waitForSelector(
        'input[type="email"] >> visible=true',
        {
          timeout: 5000,
        }
      );
      if (input) return success(true);
      else return fail('Confirmation Input not Found!');
    } catch (e: any) {
      console.log(e);
      return fail(e.message);
    }
  }
}
