import { ApiProperty, ApiPropertyOptional } from '@nestjs/swagger';
import { TaskEnum } from '../enums';
import { Result } from './result';

export class Response {
  @ApiProperty()
  taskId: number;
  @ApiProperty()
  jobId: number;
  @ApiProperty({ enum: TaskEnum })
  action: TaskEnum;
  @ApiProperty()
  success: boolean;
  result?: Result;
  @ApiProperty()
  message?: string;
  @ApiPropertyOptional()
  image?: string;
  @ApiProperty()
  duration: number;
  @ApiProperty()
  email: string;
  @ApiProperty()
  application: number;
  @ApiProperty()
  emailId: number;
  @ApiProperty()
  applicationId: number;
}
