import { Application } from '@gwarm/robot/core';
import { Response, Task, TaskEnum } from '@gwarm/views/shared';
import { Inject, Injectable, Logger } from '@nestjs/common';
import { APPLICATION_PROVIDER } from './script.module';
import { InjectChannel } from '@gwarm/packages/rabbitmq';
import { Channel } from 'amqplib';
@Injectable()
export class TaskService {
  private logger = new Logger(TaskService.name);
  constructor(
    @Inject(APPLICATION_PROVIDER) private readonly application: Application,
    @InjectChannel() private readonly channel: Channel
  ) {}

  getTask(id: number) {
    return this.application.getTask(id);
  }
  async executeTask(task: Task): Promise<Response> {
    this.application.setApplicationId(task.applicationId);
    const res = await this.application.executeTask(task);

    await this.channel.assertQueue(task.action);
    let value = await this.channel.sendToQueue(
      task.action,
      Buffer.from(JSON.stringify(res))
    );
    this.logger.log(`Send To Queue ${task.action}: ${value}`);
    value = await this.channel.sendToQueue(
      'RESPONSE',
      Buffer.from(JSON.stringify(res))
    );
    this.logger.log(`Send To Queue RESPONSE: ${value}`);
    return res;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
  }
  async stopTaskById(id: number) {
    await this.application.stopTaskById(id);
  }
  getTasks(): Task[] {
    return this.application.getTasks();
  }
  getTasksByTaskName(taskName: TaskEnum): Task[] {
    return this.application.getTasksByTaskName(taskName);
  }
  getResultByTaskId(id: number) {
    return this.application.getResultByTaskId(id);
  }
  getResponseByTaskId(id: number) {
    return this.application.getResponseByTaskId(id);
  }
}
