import { Task } from '../core';
import { ApiProperty } from '@nestjs/swagger';
import { ReplyPayload } from '../payloads';
import { TaskEnum } from '../enums';
export class ReplyTask extends Task {
  @ApiProperty({ type: TaskEnum.REPLY, default: TaskEnum.REPLY })
  override action: TaskEnum.REPLY = TaskEnum.REPLY;
  @ApiProperty()
  override payload: ReplyPayload;
}
