import { Either, fail, success } from '@gwarm/shared';
import { Response } from 'playwright-core';
import { Handler } from '../model';
import { HandlerEnum } from './handler.enum';

export class ChallengeHandler extends Handler {
  name = HandlerEnum.CHALLENGE_HANDLER;
  async handle(): Promise<Either<boolean, string>> {
    const locator = this.page.locator('div[data-challengetype="12"]');
    if ((await locator.count()) == 0) return fail('Email challenge not found');
    const available = await locator
      .nth(0)
      .getAttribute('data-challengeunavailable');
    if (available?.toString() == 'true') {
      return fail('Confirmation by email is temporary unavailable');
    }
    locator.nth(0).click();
    return success(false);
  }
  async waitFor(res: Response): Promise<boolean> {
    const payload = res.request().postData();
    if (payload) {
      return payload.includes(
        '1,\\"accounts.google.com/signin/v2/challenge/selection'
      );
    }
    return false;
  }
  async validate(): Promise<Either<boolean, string>> {
    try {
      const input = await this.page.waitForSelector(
        '[data-challengetype] >> visible=true',
        {
          timeout: 5000,
        }
      );
      if (input) return success(true);
      else return fail('Challenges not Found!');
    } catch (e: any) {
      console.log(e);
      return fail(e.message);
    }
  }
}
