import { Either, fail, success } from '@gwarm/shared';
import { Page } from 'playwright-core';
import { Message } from '../models';
import { MessagePage } from '../models/page.model';

export class MessageService {
  constructor(private readonly page: Page) {}
  async getMessages(date = -1): Promise<Either<MessagePage, string>> {
    console.time('new function');
    const messages = await this.page.evaluate((date) => {
      const messages: Message[] = [];
      const trs = document.querySelectorAll('tr[role="row"]');
      for (let i = 0; i < trs.length; i++) {
        const tr = trs.item(i);
        const box = tr.getBoundingClientRect();
        if (box.width === 0 || box.height === 0) continue;
        const fromContainer = tr.querySelector('span[email][name]');
        const subjectContainer = tr.querySelector('span[data-thread-id]');
        const dateContainer = tr.querySelector('span[title]');
        const tooltipsContainer = tr.querySelectorAll(
          'span[role="button"][data-tooltip]'
        );

        const classes = tr.getAttribute('class');
        const message = {
          from: fromContainer?.textContent ?? '',
          email: fromContainer?.getAttribute('email') ?? '',
          subject: subjectContainer?.textContent ?? '',
          dataThreadId: subjectContainer?.getAttribute('data-thread-id') ?? '',
          date: Date.parse(dateContainer?.getAttribute('title') ?? ''),
          id:
            subjectContainer?.getAttribute('data-legacy-last-message-id') ?? '',
          isRead: !classes?.includes('zE') ?? false,
          isStarred: false,
          isImportant: false,
        };

        for (let i = 0; i < tooltipsContainer.length; i++) {
          const tooltip = tooltipsContainer.item(i);
          const tooltipText = tooltip.getAttribute('aria-label');
          switch (tooltipText?.trim()) {
            case 'Not starred':
              message.isStarred = false;
              break;
            case 'Starred':
              message.isStarred = true;
              break;
            case 'Not important':
              message.isImportant = false;
              break;
            case 'Important':
              message.isImportant = true;
              break;
          }
        }
        messages.push(message);
      }
      if (messages.length === 0)
        return Promise.resolve({
          messages: messages,
          hasNext: false,
        });
      const nextContainer = document.querySelectorAll(
        'div[aria-label="Older"]'
      );
      let hasNext = !(
        nextContainer
          ?.item(nextContainer.length - 1)
          .getAttribute('aria-disabled')
          ?.toString() === 'true'
      );
      if (
        hasNext &&
        messages.length > 0 &&
        messages[messages.length - 1].date < date
      ) {
        hasNext = false;
      }

      return Promise.resolve({
        messages: messages,
        hasNext: hasNext,
      });
    }, date);
    console.timeEnd('new function');
    console.log(messages.hasNext, messages.messages.length);
    return success(messages as any);
  }

  async openMessage(message: Message) {
    console.log('[REPLY] OPEN MESSAGE');
    const url = await this.page.url();
    await this.page.goto(url + '/' + message.id);
    await this.page.waitForSelector(
      'tbody > tr:nth-child(1) > td:nth-child(4)> div:nth-child(1)[role="button"]'
    );
    console.log('[REPLY] OPEN MESSAGE DONE');
  }

  async openReplyBox() {
    console.log('[REPLY] OPEN REPLY BOX');
    const locator = await this.page.locator(
      'tbody > tr:nth-child(1) > td:nth-child(4)> div:nth-child(1)[role="button"]'
    );
    const count = await locator.count();
    if (count > 0) {
      await locator.nth(0).click();
    } else {
      throw new Error('[REPLY] Failed to find reply button');
    }
    await this.page.waitForSelector('div[data-tooltip="Show trimmed content"]');
    await this.page.waitForTimeout(2000);
  }

  async SendMessage(message: string): Promise<Either<boolean, string>> {
    console.log('[REPLY] WRITE MESSAGE');
    const done = await this.page.evaluate((msg) => {
      const context = document.querySelector('div[aria-label="Message Body"]');
      if (context) {
        context.innerHTML = msg;
        return true;
      }
      return false;
    }, message);
    if (!done) return fail('[REPLY] Failed to write message');
    await this.page.waitForTimeout(2000);
    console.log('[REPLY] SEND MESSAGE');
    await this.page.keyboard.down('Control');
    await this.page.keyboard.press('Enter');
    await this.page.keyboard.up('Control');
    await this.page.waitForTimeout(1000);
    return success(true);
  }

  async resetReplyBox() {
    console.log('[REPLY] RESET REPLY BOX');
    const locator = this.page.locator(
      'div[data-tooltip="Show trimmed content"]'
    );
    const count = await locator.count();
    if (count > 0) {
      await locator.nth(0).click();
    } else {
      throw new Error('[REPLY] Failed to find reset button');
    }
    await this.page.waitForTimeout(1000);
    console.log('[REPLY] REMOVE PREVIOUS MESSAGE');
    await this.page.keyboard.down('Control');
    await this.page.keyboard.press('A');
    await this.page.keyboard.up('Control');
    await this.page.keyboard.press('Backspace');
    console.log('[REPLY] RESET REPLY BOX DONE');
  }
}
