import { SetupRuleHandler } from '@gwarm/robot/core';
import { Email, SetupRulePayload, SetupRuleResult } from '@gwarm/views/shared';
import { BrowserContext } from 'playwright-core';

export class ProtonSetupRuleHandler extends SetupRuleHandler {
  async handle(
    context: BrowserContext,
    email: Email,
    payload: SetupRulePayload,
    result: SetupRuleResult
  ): Promise<SetupRuleResult> {
    const page = context.pages()[0];
    console.log('start');
    await page.goto('https://account.proton.me/u/0/mail/filters');
    await page.click(
      '#custom > div > div > button.button.button-solid-norm.on-mobile-mb0-5.mr1'
    );
    await page.waitForSelector('#name');
    await page.type('#name', Math.ceil(Math.random() * 10000).toString());
    await page.click(
      'body > div.modal-two > dialog > form > div.modal-two-footer > div > button'
    );
    await page.waitForSelector('select');
    console.log('select option');
    await page.selectOption('select', 'sender');
    await page.selectOption(
      'div > div:nth-child(1) > span:nth-child(2) > span > span > select',
      '!contains'
    );
    await page.type(
      'div > div:nth-child(2) > div > div > span > input',
      '|||||||||||||||||||||||||||'
    );
    await page.click('div > div:nth-child(2) > div > div > button');
    await page.waitForTimeout(500);
    await page.click('dialog > form > div.modal-two-footer > div > button');
    await page.waitForSelector('#memberSelect');
    await page.click('#memberSelect');
    await page.waitForTimeout(500);
    await page.click('div.dropdown-content > ul > li:nth-child(5) > button');
    await page.waitForTimeout(500);
    await page.click(
      'form > div.modal-two-footer > div > button.button.button-solid-norm'
    );
    await page.waitForTimeout(2000);
    return result;
  }
}
