import { BrowserContext } from 'playwright-core';
import { Handler } from './handler';
import { Response, Result, Task } from '@gwarm/views/shared';
import { Client } from '@rmp135/imgur';
const imgur = new Client('43652b743b5a7a0');

export function buildSuccessResponse(
  response: Response,
  task: Task,
  result: Result
): Response {
  response.taskId = task.id;
  response.action = task.action;
  response.jobId = task.jobId;
  response.email = task.email.email;
  response.emailId = task.emailId;
  response.applicationId = task.applicationId;
  response.success = true;
  response.result = result;
  return response;
}

export async function buildFailResponse(
  response: Response,
  task: Task,
  error: Error,
  context: BrowserContext
): Promise<Response> {
  response.taskId = task.id;
  response.action = task.action;
  response.jobId = task.jobId;
  response.email = task.email.email;
  response.emailId = task.emailId;
  response.applicationId = task.applicationId;
  response.success = false;
  response.message = error.message;
  try {
    const image = await context.pages()[0].screenshot({
      fullPage: true,
      type: 'jpeg',
    });
    const imageInfo = await imgur.Image.upload(image, {
      title: task.email.email,
      description: response.message,
    });
    response.image = imageInfo.data.link;
  } catch (e: any) {
    response.message = response.message + '\n' + e.message;
  }
  return response;
}

export class Controller<T extends Handler> {
  constructor(protected handlers = new Map<string, T>()) {}
  public registerHandler(handler: T) {
    this.handlers.set(handler.name, handler);
  }
  public getHandler(isp: string): Handler | undefined {
    return this.handlers.get(isp);
  }

  async execute(
    context: BrowserContext,
    task: Task,
    stoper: Promise<Result>,
    result: Result,
    response: Response
  ): Promise<Response> {
    const startedAt = Date.now();

    try {
      const handler = this.getHandler(task.action);
      if (!handler) throw new Error(`Handler ${task.action} not found`);
      console.log(handler);
      console.log(task.payload);
      const t = await Promise.race([
        handler.handle(context, task.email, task.payload, result),
        stoper,
      ]);
      console.log(t);
      buildSuccessResponse(response, task, result);
    } catch (error: any) {
      await buildFailResponse(response, task, error, context);
    }
    const finishedAt = Date.now();
    response.duration = finishedAt - startedAt;
    console.log(response);
    return response;
  }
}
