import { ApiProperty } from '@nestjs/swagger';
import { Response } from '../core';
import { TaskEnum } from '../enums';
import { AuthResult } from '../results';

export class AuthResponse extends Response {
  @ApiProperty({
    type: TaskEnum.AUTHENTICATION,
    default: TaskEnum.AUTHENTICATION,
  })
  override action: TaskEnum.AUTHENTICATION;
  @ApiProperty()
  override result: AuthResult;
}
