import { ApiProperty } from '@nestjs/swagger';
import { TaskEnum, IspEnum } from '../enums';
import { Email } from './email';
import { Payload } from './payload';
export class Task {
  @ApiProperty()
  id: number;
  @ApiProperty()
  jobId: number;
  @ApiProperty({ enum: TaskEnum })
  action: TaskEnum;
  @ApiProperty()
  email: Email;

  payload: Payload;
  @ApiProperty({ enum: IspEnum })
  isp: IspEnum;
  @ApiProperty()
  emailId: number;
  @ApiProperty()
  applicationId: number;
}
